import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';
require('../css/playsong.css');

var lyricData = [];
var lyricTimer;
var _self;
var lyricRes;
var songDataTime = 0;
var index = 0;
class PlaySong extends React.Component {
	constructor(props) {
    	super(props);
    	this.state = {
    		music_id: this.GetQueryString(window.location.href,'id')
    	};
  	}
  	GetQueryString(url,val) {
	　　var s = '/'+val+'\\=([^&]+)/ig';
        var re = new RegExp(eval(s));
        var result = re.exec(url);
        if(result && result.length > 0)
            return result[1];
        else
            window.location = '#/';
	}

	
	componentDidMount(){
		songDataTime = 0;
		index = 0;
		_self = this;
		$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+_self.state.music_id+".mp3")
	    	.attr('autoplay','autoplay');
			$('#music_icon').removeAttr('class');

		$.ajax({
	    	url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/song_detail.php?id='+_self.state.music_id,
	    	dataType: "jsonp",
	    	jsonpCallback:"song_detail",
	    	
	    	success:function(data){
	    		function CreateMusicDisc(){
	    			return (
	    			<div>
		    			<div className='playsongPage_bg' style={{'background':"url('"+data.songs[0].album.blurPicUrl+"') no-repeat 50%",'backgroundSize':'auto 100%'}}></div>
	                	<span className='playsongPage_logo'></span>
	                	<span className='playsongPage_needle'></span>
	                	<div className='playsongPage_disc' onClick={_self.pauseClickHandle}>
	                		<div className='disc_border PlayRotateMove'></div>
	                		<div className='disc_img PlayRotateMove'>
	                			<img src={data.songs[0].album.blurPicUrl}/>
	                		</div>
	                	</div>
	                	<div className='playsongBtn' onClick={_self.playClickHandle}></div>
	                	<p className='artistsText'>{data.songs[0].album.name} - {data.songs[0].artists[0].name}</p>
	                	<p className='lyricText'></p>
                	</div>
	                	);
	    		}
	    		ReactDOM.render(<CreateMusicDisc/>,document.getElementById('playsongPage'));
	    	}
	    })

		$.ajax({
	    	url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/lyric.php?id='+_self.state.music_id,
	    	dataType: "jsonp",
	    	jsonpCallback:"lyricCallback",
	    	
	    	success:function(data){
	    		// if(data.lrc.lyric!=''){
	    			if(data.uncollected){
	    				$('.lyricText').html("<b class='lyricShowMove'>该歌曲暂无歌词，尽情欣赏</b>");
	    			}else{
	    				if(data.nolyric){
		    				$('.lyricText').html("<b class='lyricShowMove'>该歌曲暂无歌词，尽情欣赏</b>");
		    			}else{
		    				_self.parseLyric(data);
		    			}
	    			}
	    			
            		
            	/*}else{
            		
            	}*/
	    	}
	    })
		
		

	}
	parseLyric(data){
		lyricRes = data.lrc.lyric.split('\n');
	    // $('#lyricCode ul').html('');
	        for (let l = 0; l < lyricRes.length; l++) {
	          lyricRes[l] = lyricRes[l].split(']')
	          lyricRes[l][0] = lyricRes[l][0].substr(1, 8)
	          let p = /[A-Za-z]/i
	          if (p.test(lyricRes[l][0]) || lyricRes[l][0].length !== 8 || lyricRes[l][1] === '') {
	            lyricRes.splice(l, 1)
	            l--
	          }
	        
	        }
	        lyricData = lyricRes;
	        document.getElementById('bagMusic').onplay = function(){
				_self.onplayLyric();
			}
	}
	

	onplayLyric(){
        clearInterval(lyricTimer);
        lyricTimer = setInterval(function(){
            let m = ~~(songDataTime / 100 / 60)
            let s = ~~(songDataTime / 100 % 60)
            let i = ~~(songDataTime % 100)
            m = m < 10 ? '0' + m : m
            s = s < 10 ? '0' + s : s
            i = i < 10 ? '0' + i : i
            let time = `${m}:${s}.${i}`;
            songDataTime++;
          

            if(songDataTime/100 >= document.getElementById('bagMusic').duration){
            	

            	index = 0;
                songDataTime= 0;
                clearInterval(lyricTimer);
                _self.onplayLyric();
            }
            if(time==lyricData[index][0]){
                $('.lyricText').html("<b class='lyricShowMove'>"+lyricRes[index][1]+"</b>");
                index++;

                if(index == lyricData.length){
                    
                    index = 0;
                }
            }
        },10)
	}

	musicStop(){
	    clearInterval(lyricTimer);
	}

	pauseClickHandle(event){
		$('.playsongBtn').show(100);
		$('#music_icon').addClass('mute');
		document.getElementById('bagMusic').pause();
		_self.musicStop();
		$('.playsongPage_needle').css('transform','rotate(-20deg)');
		var container = document.querySelector('.playsongPage_disc');
		var image = container.querySelector('.disc_img');
		var iTransform = getComputedStyle(image).transform;
	  	var cTransform = getComputedStyle(container).transform;
	  	container.style.transform = cTransform === 'none'
	     ? iTransform
	     : iTransform.concat(' ', cTransform);
		$('.disc_img').removeClass('PlayRotateMove');
		$('.disc_border').removeClass('PlayRotateMove');


	}

	playClickHandle(event){
		$('.playsongBtn').hide(100);
		$('#music_icon').removeAttr('class');
		document.getElementById('bagMusic').play();
		$('.playsongPage_needle').removeAttr('style');

		$('.disc_img').addClass('PlayRotateMove');
		$('.disc_border').addClass('PlayRotateMove');


	}



    render() {
        return (
                <div id='playsongPage' className='ShowMove'></div>
        );
    }
}

export default PlaySong;


