import React,{ Component } from 'react';
import { HashRouter, Route,NavLink,Switch,Redirect} from 'react-router-dom';
import Recommend from './components/Recommend';
import HotMusic from './components/HotMusic';
import Search from './components/Search';
import Header from './components/Header';
import RecommendPage from './components/RecommendPage';
import PlaySong from './components/PlaySong';
require('./css/main.css');

class App extends Component {

    render() {
    
        return (
          <HashRouter>
            <div>
            <Header/>
              <ul className='nav'>
                <li>
                  <NavLink 
                    to={{pathname: '/Recommend'}} 
                    activeClassName="selected">
                    <em></em>
                    <span>推荐音乐</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink 
                    to="/HotMusic" 
                    activeClassName="selected">
                    <em></em>
                    <span>热歌榜</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink 
                    to="/Search" 
                    activeClassName="selected">
                    <em></em>
                    <span>搜索</span>
                  </NavLink>
                </li>

              </ul>
             
                <Switch>
                  <Redirect path="/" to={{pathname:'/Recommend'}} exact={true}/> 
                  <Route path="/Recommend" component={Recommend}/>
                  <Route path="/HotMusic" component={HotMusic}/>
                  <Route path="/Search" component={Search}/>
                  <Route path="/RecommendPage" component={RecommendPage}/>
                  <Route path="/PlaySong" component={PlaySong}/>
                </Switch>
                
            </div>
              
          </HashRouter>
               
        );
    }
}

export default App;


