import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';

require('../css/recommend.css');

class Recommend extends React.Component {

	componentDidMount(){
		localStorage.hotMusicState = 0;
		var _self = this;
		var overscroll = function (els) {
        for (var i = 0; i < els.length; ++i) {
            var el = els[i];
            el.addEventListener('touchstart', function () {
                var top = this.scrollTop
                    , totalScroll = this.scrollHeight
                    , currentScroll = top + this.offsetHeight;
                if (top === 0) {
                    this.scrollTop = 1;
                } else if (currentScroll === totalScroll) {
                    this.scrollTop = top - 1;
                }
            });
            el.addEventListener('touchmove', function (evt) {
                if (this.offsetHeight < this.scrollHeight){
                    evt._isScroller = true;
                }else{
                    evt._isScroller = false;
                }
            });
        }
    };
    
    document.body.addEventListener('touchmove', function (evt) {
        if (!evt._isScroller) {
            evt.preventDefault();
        }
    });
    
    //给class为.scroll的元素加上自定义的滚动事件
    overscroll(document.querySelectorAll('.scroll'));
	//解决微信下拉的问题



		$.ajax({
	    	url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/personalized.php',
	    	type:'post',
	    	dataType: "jsonp",
	    	// jsonp:'callback',
            jsonpCallback:"successCallback",
	    	success:function(data){

	    		function NumberList(){
		    		var listItems =data.result.map((item,index) =>
				  		<li key={index} music_menu_id={item.id} onClick={_self.recommendClickHandle}>
				  			<strong style={{'background':'url('+item.picUrl+') center center / cover'}}></strong>
				  			<span>{playCountNum(item.playCount)}</span>
		            		<p>{item.name}</p>
				  		</li>
					);
					return (
						<ul>{listItems}</ul>
					);
					function playCountNum(num){
						var n = num;
						n = Math.round((n /10000) * 100) / 100;
						n = n.toFixed(1) + "万";
						return n;
					}
				}

				ReactDOM.render(<NumberList/>,document.getElementById('recommend_music_list'));
	    	}

	    })/*924680166*/




		$.ajax({
	    	url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/personalized_newsong.php',
	    	type:'post',
	    	dataType: "jsonp",
	    	jsonpCallback:"personalized_newsongCallback",
	    	success:function(data){
	    		function NewSongList(){
		    		var newSongMusicList =data.result.map((item,index) =>
				  		<li key={index} music_id={item.id} onClick={_self.newsongClickHandle}>
				  			<span>{item.name}
				  				<strong>{isAlias(item.song.alias)}</strong>
				  			</span>
		            		<b>{item.song.artists[0].name} - {item.name}</b>
				  		</li>
					);
					return (
						<ul>{newSongMusicList}</ul>
					);
					function isAlias(text){
						if(text!=''){
							return '('+text+')';
						}else{
							return '';
						}
					}
				}
				ReactDOM.render(<NewSongList/>,document.getElementById('newsong_list'));
	    	}
	    })

	}


	recommendClickHandle(event){
		event.stopPropagation();
	    if(event.target.getAttribute('music_menu_id')){
	    	window.location = '#/RecommendPage?id='+event.target.getAttribute('music_menu_id');
	    }else{
		    window.location = '#/RecommendPage?id='+event.target.parentNode.getAttribute('music_menu_id');
		    
	    }
	}


	newsongClickHandle(event){
		event.stopPropagation();
	    if(event.target.getAttribute('music_id')){
	    	startPlayMusic(event.target.getAttribute('music_id'))
	    }else{
	    	if(event.target.parentNode.getAttribute('music_id')){
		    	startPlayMusic(event.target.parentNode.getAttribute('music_id'))
		    }else{
		    	startPlayMusic(event.target.parentNode.parentNode.getAttribute('music_id'))
		    }
	    }
	    function startPlayMusic(musicId){
	    	$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+musicId+".mp3")
	    	.attr('autoplay','autoplay')
			.attr('musicId',$(this).attr('musicId'));
			$('#music_icon').removeAttr('class');
			document.getElementById('bagMusic').play();
			window.location = '#/PlaySong?id='+musicId;
	    }
	    
	}



    render() {
        return (
            <div className='router_container scroll ShowMove'>
	            <div className='recommend_container'>
	            	<h2>推荐歌单</h2>
	            	<div id='recommend_music_list'></div>
	            	<h2>最新音乐</h2>
	            	<div id='newsong_list'></div>
	            </div>
            </div>
        );
    }
}

export default Recommend;


