import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';
require('../css/recommendPage.css');
class RecommendPage extends React.Component {
	constructor(props) {
    	super(props);
    	this.state = {
    		music_id: this.GetQueryString(window.location.href,'id')
    	};
  	}

	GetQueryString(url,val) {
	　　var s = '/'+val+'\\=([^&]+)/ig';
        var re = new RegExp(eval(s));
        var result = re.exec(url);
        if(result && result.length > 0)
            return result[1];
        else
            window.location = '#/';
	}

	componentDidMount(){
		var _self = this;
		var overscroll = function (els) {
	        for (var i = 0; i < els.length; ++i) {
	            var el = els[i];
	            el.addEventListener('touchstart', function () {
	                var top = this.scrollTop
	                    , totalScroll = this.scrollHeight
	                    , currentScroll = top + this.offsetHeight;
	                if (top === 0) {
	                    this.scrollTop = 1;
	                } else if (currentScroll === totalScroll) {
	                    this.scrollTop = top - 1;
	                }
	            });
	            el.addEventListener('touchmove', function (evt) {
	                if (this.offsetHeight < this.scrollHeight){
	                    evt._isScroller = true;
	                }else{
	                    evt._isScroller = false;
	                }
	            });
	        }
	    };
	    
	    document.body.addEventListener('touchmove', function (evt) {
	        if (!evt._isScroller) {
	            evt.preventDefault();
	        }
	    });
	    
	    //给class为.scroll的元素加上自定义的滚动事件
	    overscroll(document.querySelectorAll('.scroll'));
		//解决微信下拉的问题

		$.ajax({
	    	url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/personalized_music.php?id='+_self.state.music_id,
	    	dataType: "jsonp",
	    	jsonpCallback:"personalized_musicCallback",
	    	
	    	success:function(data){

	    		function CreateMusicMenuPage(){
	    			var recommendPageMusicList = data.result.tracks.map((item,index) =>
	    				<li key={index} music_id={item.id} onClick={_self.musicClickHandle}>
        					<b>{index+1}</b>
        					<p>{item.name} 
        						<span>{isAlias(item.alias)}</span>
        					</p>
        					<strong>{item.artists[0].name} - {item.name}</strong>
        				</li>
	    				);
	    			var tagsMap = data.result.tags.map((item,index) =>
	    					<li key={index}>{item}</li>
	    				) 
	    			function isAlias(text){
						if(text!=''){
							return '('+text+')';
						}else{
							return '';
						}
					}
					

	    			return (
	    				<div>
	    				<div className='recommend_music_page_top'>
		        			<strong></strong>
		        			<i style={{'background':'url('+data.result.coverImgUrl+') center center / cover'}}></i>
		        			<div className='imgBox' style={{'background':'url('+data.result.coverImgUrl+') center center / cover'}}>
		        				<span>歌单</span>
		        			</div>
		        			<h2>{data.result.name}</h2>
		        			<div className='musicMenuAuthor'>
		        				<div className='musicMenuAuthor_img' style={{'background':'url('+data.result.creator.avatarUrl+') center center / cover'}}></div>
		        				<span>{data.result.creator.nickname}</span>
		        			</div>
		        		</div>
		        		<div className='recommend_music_page_intro'>
		        			<div className='music_menu_tags'>
		        				<span>标签 : </span>
		        				<ul>{tagsMap}</ul>
		        			</div>
		        			<div className='music_menu_description'>简介 : {data.result.description}</div>
		        		</div>
		        		<h3>歌曲列表</h3>
		        		<div id='recommend_music_page_songList'>
		        			<ul>{recommendPageMusicList}</ul>
		        		</div>
		        		</div>
	    				
	    			)


	    		}
	    		ReactDOM.render(<CreateMusicMenuPage/>,document.getElementById('recommend_music_page'));
	    	}
	    })


	}
	musicClickHandle(event){
		event.stopPropagation();
	    if(event.target.getAttribute('music_id')){
	    	startPlayMusic(event.target.getAttribute('music_id'))
	    }else{
	    	if(event.target.parentNode.getAttribute('music_id')){
		    	startPlayMusic(event.target.parentNode.getAttribute('music_id'))
		    }else{
		    	startPlayMusic(event.target.parentNode.parentNode.getAttribute('music_id'))
		    }
	    }
	    function startPlayMusic(musicId){
	    	$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+musicId+".mp3")
	    	.attr('autoplay','autoplay')
			.attr('musicId',$(this).attr('musicId'));
			$('#music_icon').removeAttr('class');
			document.getElementById('bagMusic').play();
			window.location = '#/PlaySong?id='+musicId;
/*			window.location = '#/RecommendPage?id='+event.target.parentNode.getAttribute('music_menu_id');*/
	    }
	}
    render() {
        return (
        	<div id='recommend_music_page' className='scroll ShowMove'></div>
        );
    }
}

export default RecommendPage;


