import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';
require('../css/search.css');

class Search extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			value:''
		};
	}


	componentDidMount(){
		var _self = this;
		localStorage.hotMusicState = 0;
		// localStorage.searchTrace = ['南山南','不能说的秘密','My Soul','Luv Letter'];
		if(localStorage.searchTrace){
			if(localStorage.searchTrace.split(",").length>0){
				function ReturnTraceList(){
					var listItems = localStorage.searchTrace.split(",").map((item,index) =>
							<li key={index} index={index} onClick={_self.traceClick} content={item}>
					  			<b>{item}</b>
					  			<span types='close'></span>
					  		</li>

					);
					return (
						<ul>{listItems}</ul>
					); 1
				}
				ReactDOM.render(<ReturnTraceList/>,document.getElementById('searchTrace'));

			}
		}else{
			$('#searchTrace').html('<h2>暂无搜索记录</h2>');
		}
		
		



		var overscroll = function (els) {
        for (var i = 0; i < els.length; ++i) {
            var el = els[i];
            el.addEventListener('touchstart', function () {
                var top = this.scrollTop
                    , totalScroll = this.scrollHeight
                    , currentScroll = top + this.offsetHeight;
                if (top === 0) {
                    this.scrollTop = 1;
                } else if (currentScroll === totalScroll) {
                    this.scrollTop = top - 1;
                }
            });
            el.addEventListener('touchmove', function (evt) {
                if (this.offsetHeight < this.scrollHeight){
                	evt._isScroller = true;
                }else{
                    evt._isScroller = false;
                }
            });
        }
    };
    
    document.body.addEventListener('touchmove', function (evt) {
        if (!evt._isScroller) {
            evt.preventDefault();
        }
    });
    
    //给class为.scroll的元素加上自定义的滚动事件
    overscroll(document.querySelectorAll('.scroll'));





	    
	}


	/*focusHandle(event){
		event.target.style.cssText += "width:80%;left:45%";
		$('#tinggeshiqu').css('left','-100%');
		// $('#music_icon').hide(300);
		$('#search_close').css('right','2.5%');
		$('#searchTrace').fadeIn(100);
	}*/
	traceClick(event){
		
		event.stopPropagation();
	    
	    if(event.target.getAttribute('types')){
	    	
			if(event.target.parentNode.getAttribute('index')==0){
				event.target.parentNode.style.display="none";
				localStorage.searchTrace=localStorage.searchTrace.split(event.target.previousElementSibling.innerHTML+',').join("");
			}else{
				event.target.parentNode.style.display="none";
				localStorage.searchTrace=localStorage.searchTrace.split(','+event.target.previousElementSibling.innerHTML).join("");
			}
	    }else{
	    	if(event.target.getAttribute('content')){
		    	tracePlay(event.target.getAttribute('content'))
		    }else{
		    	if(event.target.parentNode.getAttribute('content')){
			    	tracePlay(event.target.parentNode.getAttribute('content'))
			    }else{
			    	tracePlay(event.target.parentNode.parentNode.getAttribute('content'))
			    }
		    }
	    }
	    function tracePlay(musicName){
	    	$.ajax({
				url:"http://fre2dom.top/reactApp/my-app/src/php_get_contents/search.php?val="+musicName,
				dataType: "json",
				// jsonpCallback:"searchCallback",
				success:function(data){
					$('#searchTrace').fadeOut(100);
					$('#searchResult').fadeIn(100);
					$('#searchResult').html('');
					var musicListStr = '';
					for(let i in data.result.songs){
                		musicListStr+="<li musicId="+data.result.songs[i].id+"><b>"+data.result.songs[i].name+"</b><span>"+data.result.songs[i].artists[0].name+"</span></li>";
                	}

					$('#searchResult').append("<ul>"+ musicListStr + "</ul>");
					$('#searchResult ul li').on('click',function(){
				    	$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+$(this).attr('musicId')+".mp3")
				    		.attr('musicId',$(this).attr('musicId'));
				    		$('#music_icon').removeAttr('class');
				    		document.getElementById('bagMusic').play();
				    		window.location = '#/PlaySong?id='+$(this).attr('musicId');

				    })
				}
			})
	    }
	}


	inputHandle(event){
		this.setState({
			value:event.target.value
		})
		if(event.target.value.length>0){
			$('#searchTrace').fadeOut(100);
			$('#searchResult').fadeIn(100);
		}else{
			$('#searchTrace').fadeIn(100);
			$('#searchResult').fadeOut(100);
		}
		$('#searchResult').html('');
		$.ajax({
			url:"http://fre2dom.top/reactApp/my-app/src/php_get_contents/search.php?val="+this.state.value,
			dataType: "json",
			// jsonpCallback:"searchCallback",
			success:function(data){
				if(data.result){
						
						var musicListStr = '';

						for(let i in data.result.songs){
	                		musicListStr+="<li musicId="+data.result.songs[i].id+"><b>"+data.result.songs[i].name+"</b><span>"+data.result.songs[i].artists[0].name+"</span></li>";
	                	}

						$('#searchResult').append("<ul>"+ musicListStr + "</ul>");
						$('#searchResult ul li').on('click',function(){

							
							if(localStorage.searchTrace==''){
								localStorage.searchTrace=$(this).children('b').text();
							}else{
								localStorage.searchTrace=$(this).children('b').text()+','+localStorage.searchTrace;
							}
					    	$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+$(this).attr('musicId')+".mp3")
					    		.attr('musicId',$(this).attr('musicId'));
					    		$('#music_icon').removeAttr('class');
					    		document.getElementById('bagMusic').play();
					    		window.location = '#/PlaySong?id='+$(this).attr('musicId');

					    })
				}else{
					$('#searchResult').text('搜索结果为空');
				}
			}
		})
	}


    render() {
        return (
            <div id='searchPage' className='ShowMove'>
            	<input type='search' 
            			value={this.state.value} 
            			onFocus={this.focusHandle} 
            			onInput={this.inputHandle.bind(this)} 
            			placeholder='搜索歌曲、歌手、专辑'/>
            	<div id='searchTrace' className='scroll'></div>
            	<div id='searchResult' className='scroll'></div>
            </div>
        );
    }
}

export default Search;


