import React from 'react';
import $ from 'jquery';
import ReactDOM from 'react-dom';
require('../css/hotmusic.css');
class HotMusic extends React.Component {
	componentDidMount(){
		var _self = this;
	

		$.ajax({
			url:'http://fre2dom.top/reactApp/my-app/src/php_get_contents/hotmusic.php?id=3778678',
	    	dataType: "jsonp",
	    	jsonpCallback:"hotmusicCallback",
	    	success:function(data){
	    		function HotmusicPage(){
	    			var hotMusicList = data.result.tracks.map((item,index) =>
	    				<li key={index} music_id={item.id} onClick={_self.musicClickHandle}>
        					<b>{index+1}</b>
        					<p>{item.name} 
        						<span>{isAlias(item.alias)}</span>
        					</p>
        					<strong>{item.artists[0].name} - {item.name}</strong>
        				</li>
	    				);
	    			var tagsMap = data.result.tags.map((item,index) =>
	    					<li key={index}>{item}</li>
	    				) 
	    			function isAlias(text){
						if(text!=''){
							return '('+text+')';
						}else{
							return '';
						}
					}
						function getLocalTime(nS) {     
						   return new Date(parseInt(nS) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');     
						} 

						$('#hotmusic_container').css('overflow-y','scroll');
						$('body').css('overflow','auto').css('overflow-y','scroll');

	    			return (
	    				<div>
		    				<div className='hotmusicTopImg'>
			            		<span></span>
			            		<b>更新日期 : 12月28日</b>
			            	</div>
			        		<div id='hotMusicBox'>
			        			<ul>{hotMusicList}</ul>
			        		</div>
	    				</div>
	    			)
	    		};

	    		ReactDOM.render(<HotmusicPage/>,document.getElementById('hotmusic_container'));
	    			
	    			if(localStorage.hotMusicState ==0){
	    				window.location.reload();
	    				localStorage.hotMusicState = 1;
	    			}else{
	    				localStorage.hotMusicState = 1;
	    			}
	    		
	    	}
		})
		;
	}
	musicClickHandle(event){
		event.stopPropagation();
	    if(event.target.getAttribute('music_id')){
	    	startPlayMusic(event.target.getAttribute('music_id'))
	    }else{
	    	if(event.target.parentNode.getAttribute('music_id')){
		    	startPlayMusic(event.target.parentNode.getAttribute('music_id'))
		    }else{
		    	startPlayMusic(event.target.parentNode.parentNode.getAttribute('music_id'))
		    }
	    }
	    function startPlayMusic(musicId){
	    	$('#bagMusic').attr('src',"http://music.163.com/song/media/outer/url?id="+musicId+".mp3")
			.attr('musicId',$(this).attr('musicId'));
			$('#music_icon').removeAttr('class');
			document.getElementById('bagMusic').play();
			window.location = '#/PlaySong?id='+musicId;
	    }
	}
    render() {
        return (
            <div id='hotmusic_container' className='ShowMove'>
            	

            </div>
        );
    }
}

export default HotMusic;


